import User from './components/User.vue';
import Home from './components/Home.vue';
import List from './components/List.vue';
import Create from './components/Create.vue';
import Shield from './components/Shield.vue';



const routes = [
  {
    path: '/home',
    component: Home,
    name: 'Home',
    props: true
  },

  {
    path: '/user',
    component: User,
    name: 'User',
    props: true
  },

  {
    path: '/list',
    component: List,
    name: 'List',
    props: true
  },

  {
    path: '/create',
    component: Create,
    name: 'Create',
    props: true
  },

  {
    path: '/shield',
    component: Shield,
    name: 'Shield',
    props: true
  },

]

export default routes;