<template>
  <div>

    <Create />

    <div class="listTask">
      <table class="table table-primary">

        <thead>
          <tr>
            <th scope="col">Cong viec</th>
            <th scope="col">Trang Thai</th>
            <th scope="col">Thoi gian </th>
            <th scope="col">Sua</th>
            <th scope="col">Xoa</th>
          </tr>
        </thead>

        <tbody>
          <tr v-for="(task, index) in tasks" :key="index"
            :class="{
              'table-success': task.status === 'da hoan thanh',
              'table-warning': task.status === 'dang thuc hien',
              'table-danger': task.status === 'chua hoan thanh'
            }"
          >
            <th :class="{lineThrough: task.status === 'da hoan thanh'}">
            {{ task.name }}</th>

            <th @click.prevent = "changeStatus(index)"
              class="status pointer minWidth">
              <a class="link" href="#">{{ task.status }}</a>
            </th>

            <th>{{ task.time }}</th>

            <td>
              <font-awesome-icon icon="fa-solid fa-pen"
              @click="editTask(index)"
              class="pointer" />
            </td>

            <td>
              <font-awesome-icon icon="fa-solid fa-trash"
              @click="deleteTask(index)"
              class="pointer" />
            </td>

          </tr>
        </tbody>

      </table>
    </div>  
    
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex';
import Create from './Create.vue';

export default {

  computed: {
    ...mapState(['tasks', 'taskcreate', 'editedTask', 'contentBtn'])
  },
  methods: {
    ...mapActions(["deleteTask", "editTask", "changeStatus"]),
    
  },
  components: { Create }
}
</script>

<style>
.pointer {
  cursor: pointer;
}

.link {
  color: black;
  text-decoration: none;
}

.link:hover {
  color:chocolate;
}

.listTask {
  margin-top: 30px;
}

.table thead > tr > th:nth-child(2) {
  min-width: 150px;
}

.table thead > tr > th:nth-child(1) {
  min-width: 200px;
}

</style>