<script>

export default {
  components: {
    
  },
  data() {
    return {
      display: false,
  
    }
  },
  mounted() {
    let list = document.querySelectorAll('.list');
      for (let i = 0; i < list.length; i++) {
        list[i].onclick = () => {
          let j = 0;
          while (j < list.length) {
            list[j++].className = 'list';
          }
          list[i].className = 'list active'
        }
      }
  },
  methods: {
    togglemenu() {
      this.display = !this.display;
    }
  }
}
</script>

