export default {
  deleteTask(state, index) {
    state.tasks.splice(index, 1);
  },

  changeStatus(state, index) {
    let newIndex = state.allStatus.indexOf(state.tasks[index].status);
    if(++newIndex > state.allStatus.length - 1) newIndex = 0;
    state.tasks[index].status = state.allStatus[newIndex];
  },

  addTask(state) {
    if (state.taskcreate.name == '' || state.taskcreate.time == '') return;
    if (state.editedTask === null) {
      state.tasks.push({
        name: state.taskcreate.name,
        status: 'chua hoan thanh',
        time: state.taskcreate.time
      })
      state.taskcreate.name = '';
      state.taskcreate.time = '';
    }

    else {
      state.tasks[state.editedTask].name = state.taskcreate.name;
      state.tasks[state.editedTask].time = state.taskcreate.time;
      state.editedTask = null;
      state.taskcreate.name = '';
      state.taskcreate.time = '';
      state.contentBtn = 'Them';
    }
  },

  editTask(state, index) {
    state.taskcreate.name = state.tasks[index].name;
    state.taskcreate.time = state.tasks[index].time;
    state.editedTask = index;
    state.contentBtn = "Xong";
  },

  
}