import Vue from 'vue';
import App from './App.vue';
Vue.config.productionTip = false;

import "bootstrap/dist/css/bootstrap.min.css";

import { library } from '@fortawesome/fontawesome-svg-core';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
import { faHouse, faListCheck, faUser, faPlus, faShieldHalved, faPen, faTrash, faAnglesLeft } from '@fortawesome/free-solid-svg-icons';
library.add(faHouse, faListCheck, faUser, faPlus, faShieldHalved, faPen, faTrash, faAnglesLeft);
Vue.component('font-awesome-icon', FontAwesomeIcon)

import VueRouter from 'vue-router';
Vue.use(VueRouter);


import routes from './Routes';
import store from './store';

const router = new VueRouter({
  
  routes,
})



new Vue({
  render: h => h(App),
  router,
  store
}).$mount('#app')
